import title from '../assets/title.png';
import logoMobile from '../assets/logo-mobile.png';
import { html } from 'htm/preact';
import { useEffect, useRef } from 'preact/hooks';

export default function About() {
    const titleRef = useRef(null);

    const centerLogo = () => {
        if (! titleRef || ! titleRef.current) return;
        const viewportHeight = window.innerHeight || document.documentElement.clientHeight;
        const titleHeight = titleRef.current.offsetHeight;
        const margin = (viewportHeight / 2) - titleHeight - 36;
        titleRef.current.style.marginBottom = `${margin * 2}px`;
    };

    useEffect(() => {
        document.body.classList.add('about');
        document.body.classList.remove('home');
        setTimeout(centerLogo, 50);
        const debounceCenterLogo = debounce(centerLogo, 50);
        window.addEventListener('resize', debounceCenterLogo);
        return () => window.removeEventListener('resize', debounceCenterLogo);
    }, []);

    return html`
        <div id="about" class="container">
            <section class="centered">
                <img class="title title-mobile" src="${logoMobile}" alt="CHILDREN OF LIGHT" />
                <img ref="${titleRef}" class="title" src="${title}" alt="CHILDREN OF LIGHT" />
                <div class="mission">
                    <p>CHILDREN OF LIGHT is an agency focused on building brands globally.</p>
                    <p>We are dedicated to leveraging brand intelligence to develop meaningful content and engage people with actionable brand experiences that spark human connection in this digital era.</p>
                    <p>CHILDREN OF LIGHT turns stories into conversations, impressions into relationships, and campaigns into results. We partner with companies to bring clarity to their brand identity in order to build a personalised marketing plan and foster genuine customer experiences.</p>
                </div>
            </section>
            <section class="contact">
                <h2>Contact</h2>
                <ul>
                    <li>
                        <h3>Paris</h3>
                        <a href="mailto:ting@childrenoflight.net">ting@childrenoflight.net</a>
                    </li>
                     <li>
                        <h3>Bejing</h3>
                        <a href="mailto:vicky@childrenoflight.net">vicky@childrenoflight.net</a>
                    </li>
                     <li>
                        <h3>Los Angeles</h3>
                        <a href="mailto:info@childrenoflight.net">info@childrenoflight.net</a>
                    </li>
                </ul>
            </section>
            <section class="clients">
                <h2>Selected Clients</h2>
                <ul>
                    <li class="clients-list">
                        <span>Adidas</span>
                        <span>Asics</span>
                        <span>C2h4</span>
                        <span>Dazed</span>
                        <span>Fiorucci</span>
                        <span>Kappa</span>
                        <span>Kenzo</span>
                    </li>
                    <li class="clients-list">
                        <span>Leisure Center</span>
                        <span>Levis</span>
                        <span>Li-ning</span>
                        <span>Mastermind</span>
                        <span>Misbhv</span>
                        <span>Nike</span>
                        <span>Nowness</span>
                    </li>
                    <li class="clients-list">
                        <span>Vans</span>
                        <span>Vetements</span>
                        <span>Vivienne Westwood</span>
                        <span>Wallpaper</span>
                        <span>Wsj</span>
                        <span>Xander Zhou</span>
                        <span>Y/project</span>
                    </li>
                    <li class="clients-list-mobile">
                        <span>Adidas</span>
                        <span>Asics</span>
                        <span>C2h4</span>
                        <span>Dazed</span>
                        <span>Fiorucci</span>
                        <span>Kappa</span>
                        <span>Kenzo</span>
                        <span>Leisure Center</span>
                        <span>Levis</span>
                        <span>Li-ning</span>
                        <span>Mastermind</span>
                    </li>
                     <li class="clients-list-mobile">
                        <span>Misbhv</span>
                        <span>Nike</span>
                        <span>Nowness</span>
                        <span>Vans</span>
                        <span>Vetements</span>
                        <span>Vivienne Westwood</span>
                        <span>Wallpaper</span>
                        <span>Wsj</span>
                        <span>Xander Zhou</span>
                        <span>Y/project</span>
                    </li>
                </ul>
            </section>
            <footer>
                <ul class="socials">
                    <li><a href="#">Instagram</a></li>
                    <li><a href="#">Facebook</a></li>
                    <li><a href="#">Wechat</a></li>
                </ul>
                <p class="copyright">CHILDREN OF LIGHT  2020</p>
            </footer>
        </div>
    `;
}

function debounce(func, wait, immediate) {
    let timeout;
    return function() {
        let context = this, args = arguments;
        let later = function() {
            timeout = null;
            if (!immediate) func.apply(context, args);
        };
        let callNow = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) func.apply(context, args);
    };
}
